/*---------------
  	OVERLAYS
---------------*/
.image-overlay {
  width: 100%;
  height: 100%;
  background-color: rgba(25, 31, 32, 0.9);
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  -webkit-transition: all 0.3s ease-in;
  transition: all 0.3s ease-in;
  cursor: pointer;
}

.image-overlay .clickable-icon {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -35px;
  margin-top: -35px;
  -webkit-transform: scale(0);
  transform: scale(0);
  -webkit-transition-delay: 0.15s;
  transition-delay: 0.15s;
  -webkit-transition: -webkit-transform 0.2s ease-in;
  transition: -webkit-transform 0.2s ease-in;
  transition: transform 0.2s ease-in;
  transition: transform 0.2s ease-in, -webkit-transform 0.2s ease-in;
}

.image-overlay .clickable-icon .svg-plus {
  fill: #fff;
  width: 28px;
  height: 28px;
  position: absolute;
  top: 21px;
  left: 21px;
}

.image-overlay .clickable-icon.primary {
  background-color: #00d7b3;
}

.image-overlay .clickable-icon.secondary {
  background-color: #1396e1;
}

.image-overlay .clickable-icon.tertiary {
  background-color: #e61852;
}

